(function (angular) {
  'use strict';

  angular
      .module('good-as-gold.home')
      .config(configureApp);

  /* @ngInject */
  function configureApp($routeProvider) {
    $routeProvider
        .when('/', {
          templateUrl: '/assets/js/home/partials/main.html',
          controller: 'HomeCtrl'
        })
  }

})(window.angular);
